<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ÂïÜÂìÅÂàóË°®</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáËßÜÂõæÂå∫Âüü -->
    <el-card>
      <el-row>
        <el-col>
          <el-Button type="primary" @click="showAddDialog">Ê∑ªÂä†ÂàÜÁ±ª</el-Button>
        </el-col>
      </el-row>

      <!-- ÂàóË°®Âå∫Âüü -->
      <tree-table
        class="tree-table"
        :data="catelist"
        :columns="columns"
        :expand-type="false"
        :selection-type="false"
        show-index
        index-text="#"
