          ></i>
          <i class="el-icon-error" v-else style="color:red;"></i>
        </template>
        <template slot="order" slot-scope="scope">
          <el-tag size="mine" v-if="scope.row.cat_level === 0">‰∏ÄÁ∫ß</el-tag>
          <el-tag size="mine" type="success" v-else-if="scope.row.cat_level === 1">‰∫åÁ∫ß</el-tag>
          <el-tag size="mine" type="warning" v-else>‰∏âÁ∫ß</el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditDialogVisible(scope.row.cat_id)"
          >ÁºñËæë</el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="RemoveCateById(scope.row.cat_id)"
          >Âà†Èô§</el-button>
        </template>
      </tree-table>
      <!-- ÂàÜÈ°µÁªÑ‰ª∂ -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 7, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- Ê∑ªÂä†ÂàÜÁ±ªÂØπËØùÊ°Ü -->
    <el-dialog
      title="Ê∑ªÂä†ÂàÜÁ±ª"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addCateDialogClose"
    >
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="ÂàÜÁ±ªÂêçÁß∞Ôºö" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="Áà∂Á∫ßÂàÜÁ±ªÔºö">
          <!-- Ôºöoptions Áî®Êù•ÊåáÂÆöÊï∞ÊçÆÊ∫ê -->
          <!-- props Áî®Êù•ÊåáÂÆöÈÖçÁΩÆÂØπË±° -->
          <!-- v-model ÂøÖÈ°ªÊòØ‰∏Ä‰∏™Êï∞ÁªÑ -->
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps "
            @change="parentCateChange"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="addCate">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
    <!-- ÁºñËæëÂàÜÁ±ªÂØπËØùÊ°Ü -->
    <el-dialog title="ÁºñËæëÂàÜÁ±ª" :visible.sync="editDialogVisible" width="50%">
      <el-form ref="editCateFormRef" label-width="100px" :model="addCateForm">
        <el-form-item label="ÂàÜÁ±ªID" prop="cat_pid">
          <el-input v-model="editForm.cat_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="ÂàÜÁ±ªÂêçÁß∞" prop="cat_name">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="editCateInfo">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { type } from 'os'
export default {
  data() {
    return {
      // ÂïÜÂìÅÂàÜÁ±ªÁöÑÊï∞ÊçÆÂàóË°®,ÈªòËÆ§‰∏∫Á©∫
      catelist: [],
      // Ëé∑ÂèñÊï∞ÊçÆÁâµÊ∂âÂà∞ÂàÜÈ°µÔºåÊâÄ‰ª•ÂÆö‰πâ‰∏Ä‰∏™queryInfo
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // ÊÄªÊï∞ÊçÆÊù°Êï∞
      total: 0,
      // ‰∏∫tableÊåáÂÆöÂàóÁöÑÂÆö‰πâ
      columns: [
        {
          label: 'ÂàÜÁ±ªÂêçÁß∞',
          prop: 'cat_name'
        },
        {
          label: 'ÊòØÂê¶ÊúâÊïà',
          // Ë°®Á§∫Â∞ÜÂΩìÂâçÂàóÂÆö‰πâ‰∏∫Ê®°ÊùøÂàó
          type: 'template',
          // Ë°®Á§∫ÂΩìÂâçËøô‰∏ÄÂàó‰ΩøÁî®Ê®°ÊùøÂêçÁß∞
          template: 'isok'
        },
        {
          label: 'ÊéíÂ∫è',
          // Ë°®Á§∫Â∞ÜÂΩìÂâçÂàóÂÆö‰πâ‰∏∫Ê®°ÊùøÂàó
          type: 'template',
          // Ë°®Á§∫ÂΩìÂâçËøô‰∏ÄÂàó‰ΩøÁî®Ê®°ÊùøÂêçÁß∞
          template: 'order'
        },
        {
          label: 'Êìç‰Ωú',
          // Ë°®Á§∫Â∞ÜÂΩìÂâçÂàóÂÆö‰πâ‰∏∫Ê®°ÊùøÂàó
          type: 'template',
          // Ë°®Á§∫ÂΩìÂâçËøô‰∏ÄÂàó‰ΩøÁî®Ê®°ÊùøÂêçÁß∞
          template: 'opt'
        }
      ],
      // ÊòØÂê¶Â±ïÁ§∫Ê∑ªÂä†ÂØπËØùÊ°ÜÁöÑ
      addDialogVisible: false,
      // Ê∑ªÂä†ÂàÜÁ±ªÁöÑË°®ÂçïÊï∞ÊçÆÂØπË±°
      addCateForm: {
        // Â∞ÜË¶ÅÊ∑ªÂä†ÁöÑÂàÜÁ±ªÂêçÁß∞
        cat_name: '',
        // Áà∂Á∫ßÁöÑÂàÜÁ±ªID
        cat_pid: '',
        // Áà∂Á±ªÁöÑÁ≠âÁ∫ßÔºåÈªòËÆ§Ë¶ÅÊ∑ªÂä†ÁöÑÊòØ1Á∫ßÂàÜÁ±ª
        cat_level: 0
      },
      // Ê∑ªÂä†ÂàÜÁ±ªË°®ÂçïÁöÑÈ™åËØÅËßÑÂàô
      addCateFormRules: {
        cat_name: [{ required: true, message: 'ËØ∑ËæìÂÖ•ÂàÜÁ±ªÂêçÁß∞', trigger: 'blur' }]
      },
      // Áà∂Á∫ßÂàÜÁ±ªÁöÑÂàóË°®
      parentCateList: [],
      // ÊåáÂÆöÁ∫ßËÅîÈÄâÊã©Âô®ÁöÑÈÖçÁΩÆÂØπË±°
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // ÈÄâ‰∏≠ÁöÑÁà∂Á∫ßÂàÜÁ±ªÁöÑIDÊï∞ÁªÑ
      selectedKeys: [],
      // ÁºñËæëÂàÜÁ±ªÂØπÁîªÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      editDialogVisible: false,
      // ÁºñËæëÂàÜÁ±ªÁöÑË°®ÂçïÊï∞ÊçÆ
      editForm: {}
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // Ëé∑ÂèñÂïÜÂìÅÂàÜÁ±ªÁöÑÊï∞ÊçÆ
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('üò´Ëé∑ÂèñÂïÜÂìÅÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
      // ÊääÊï∞ÊçÆÂàóË°®ÔºåËµãÂÄºÁªôcatelist
      this.catelist = res.data.result

      // ‰∏∫ÊÄªÊï∞ÊçÆÊù°Êï∞ËµãÂÄº
      this.total = res.data.total
    },
    // ÁõëÂê¨pagesizeÊîπÂèò
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
      this.getCateList()
    },
    // ÁõëÂê¨pagenumÊîπÂèò
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    // ÁÇπÂáªÊåâÈíÆ ÔºåÂºπÂá∫Ê∑ªÂä†ÂàÜÁ±ªÂØπËØùÊ°Ü
    showAddDialog() {
      // ÂÖàËé∑ÂèñÊâÄÊúâÁöÑÁà∂Á∫ßÂàÜÁ±ªÁöÑÊï∞ÊçÆÂàóË°®
      this.getParentCateList()
      // ÂÜçÊòæÁ§∫ÂØπËØùÊ°Ü
      this.addDialogVisible = true
    },
    // Ëé∑ÂèñÁà∂Á±ªÂàÜÁ±ªÁöÑÂàóË°®
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) return this.$message.error('üò´Ëé∑ÂèñÁà∂Á∫ßÂàóË°®Â§±Ë¥•ÔºÅ')

      this.parentCateList = res.data
      console.log(this.parentCateList)
    },
    // ÈÄâÊã©È°πÂèëÁîüÂèòÂåñÔºåËß¶ÂèëËøô‰∏™ÂáΩÊï∞
    parentCateChange() {
      console.log(this.selectedKeys)
      // Â¶ÇÊûúselectedKeys Êï∞ÁªÑ‰∏≠ÁöÑlengthÂ§ß‰∫é0ÔºåËØÅÊòéÈÄâ‰∏≠ÁöÑÁà∂Á∫ßÂàÜÁ±ª
      // Âèç‰πã„ÄÇÂ∞±ËØ¥ÊòéÊ≤°ÊúâÈÄâ‰∏≠‰ªª‰ΩïÁà∂Á∫ßÂàÜÁ±ª
      if (this.selectedKeys.length > 0) {
        // Áà∂Á∫ßÂàÜÁ±ªÁöÑID
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // ‰∏∫ÂΩìÂâçÂàÜÁ±ªÁöÑÁ≠âÁ∫ßËµãÂÄº
        this.addCateForm.cat_level = this.selectedKeys.length
        return
      } else {
        this.addCateForm.cat_pid = 0

        // ‰∏∫ÂΩìÂâçÂàÜÁ±ªÁöÑÁ≠âÁ∫ßËµãÂÄº
        this.addCateForm.cat_level = 0
      }
    },
    // ÁÇπÂáªÁ°ÆÂÆöÊåâÈíÆ ÔºåÂèëÈÄÅËØ∑Ê±Ç
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('üò´Ê∑ªÂä†ÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
        }
        this.getCateList()
        this.$message.success('üéâÊ∑ªÂä†ÂàÜÁ±ªÊàêÂäüÔºÅ')
        this.addDialogVisible = false
      })
      console.log(this.addCateForm)
    },
    // ÁõëÂê¨ÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂ÔºåÈáçÂà∂Ë°®ÂçïÊï∞ÊçÆ
    addCateDialogClose() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },
    // Âà†Èô§ÂàÜÁ±ª
    async RemoveCateById(id) {
      const confirmResult = await this.$confirm('Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•ÂàÜÁ±ª, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('Â∑≤ÁªèÂèñÊ∂àÂà†Èô§')
      const { data: res } = await this.$http.delete(`categories/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('üò´Âà†Èô§Â§±Ë¥•ÔºÅ')
      }
      this.getCateList()
      this.$message.success('üéâÂà†Èô§ÊàêÂäüÔºÅ')
    },
    // ÁºñËæëËßíËâ≤ÂØπËØùÊ°Ü
    async showEditDialogVisible(id) {
      const { data: res } = await this.$http.get(`categories/${id}`)
      if (res.meta.status !== 200) {
        this.$message.error('Ëé∑Âèñüé≠ÂàÜÁ±ª‰ø°ÊÅØÂ§±Ë¥•')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // ÁºñËæëÂàÜÁ±ª‰ø°ÊÅØÁöÑÁ°ÆÂÆöÊåâÈíÆÔºåÁÇπÂáªÊåâÈíÆÔºåÊèê‰∫§‰øÆÊîπ‰ø°ÊÅØ
    editCateInfo() {
      this.$refs.editCateFormRef.validate(async valid => {
        if (!valid) return
        // ÂèØ‰ª•ÂêëÊúçÂä°Âô®ÂèëÈÄÅÁºñËæëËØ∑Ê±Ç‰∫Ü
        const { data: res } = await this.$http.put(`categories/${this.editForm.cat_id}`, {
          cat_id: this.editForm.cat_id,
          cat_name: this.editForm.cat_name
        })
        console.log(res.data)
        if (res.meta.status !== 200) {
          return this.$message.error('üò´ÁºñËæëÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
        }
        this.getCateList()
        this.editDialogVisible = false
        this.$message.success('üéâÁºñËæëÂàÜÁ±ªÊàêÂäüÔºÅ')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.tree-table {
  margin-top: 15px;
}
